  background-image:-webkit-gradient(linear,left top, left bottom,from(rgba(0,0,0,0.85)),to(rgba(0,0,0,0.85))),url(Images/bricks-min.jpg);
  background-image:-webkit-linear-gradient(rgba(0,0,0,0.85),rgba(0,0,0,0.85)),url(Images/bricks-min.jpg);
  background-image:-o-linear-gradient(rgba(0,0,0,0.85),rgba(0,0,0,0.85)),url(Images/bricks-min.jpg);
  background-image:linear-gradient(rgba(0,0,0,0.85),rgba(0,0,0,0.85)),url(Images/bricks-min.jpg);
  background-size: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}



