@font-face {
  font-family: 'fontello';
  src: url('../font/fontello.eot?40642034');
  src: url('../font/fontello.eot?40642034#iefix') format('embedded-opentype'),
       url('../font/fontello.woff2?40642034') format('woff2'),
       url('../font/fontello.woff?40642034') format('woff'),
       url('../font/fontello.ttf?40642034') format('truetype'),
       url('../font/fontello.svg?40642034#fontello') format('svg');
  font-weight: normal;
  font-style: normal;
}
